import $ from "jquery";

import bootstrap from "bootstrap";

import popper from "popper.js";

$("#openModal").on(function() {
    $("#myModal").css("display", "block");
  });

  //       
  $(".close").on(function() {
    $("#myModal").css("display", "none");
  });

  //        
  $(window).on(function(event) {
    if (event.target == $("#myModal")[0]) {
      $("#myModal").css("display", "none");
    }
  });